<style>
.icon-add-wrap-i {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap !important;
}
.group-buy-add-wraper {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  border: 1px solid #ececec;
  padding: 5px 8px;
  margin-right: 10px;
}
.act-img-wrap {
  display: flex;
  align-items: center;
}
.act-img-w-h {
  width: 46px;
  height: 46px;
}
.act-tip-txt {
  padding: 5px 10px 5px 6px;
  font-size: 12px;
  line-height: 22px;
}
.group-buy-add-wraper:hover {
  border: 1px solid #3080fe;
}
</style>

<template>
  <div>
    <div class="handel-search-wraper">
      <div class="handel-wraper icon-add-wrap-i">
        <div class="group-buy-add-wraper">
          <div class="act-img-wrap">
            <img src="/shop/static/imgs/gb.png" alt class="act-img-w-h" />
          </div>
          <div class="act-tip-txt">
            <p>拼团活动</p>
            <p>引流&促销</p>
          </div>
          <div>
            <el-button type="primary" class="sup-mini" @click="addGbPage" size="mini">立即创建</el-button>
          </div>
        </div>

        <div class="group-buy-add-wraper">
          <div class="act-img-wrap">
            <img src="/shop/static/imgs/tb.png" alt class="act-img-w-h" />
          </div>
          <div class="act-tip-txt">
            <p>秒杀活动</p>
            <p>引流&促销</p>
          </div>
          <div>
            <el-button type="primary" class="sup-mini" @click="addTbPage" size="mini">立即创建</el-button>
          </div>
        </div>
        <div class="group-buy-add-wraper">
          <div class="act-img-wrap">
            <img src="/shop/static/imgs/dis.png" alt class="act-img-w-h" />
          </div>
          <div class="act-tip-txt">
            <p>爆款活动</p>
            <p>引流&促销</p>
          </div>
          <div>
            <el-button type="primary" class="sup-mini" @click="addBKPage" size="mini">立即创建</el-button>
          </div>
        </div>
        <!-- <div class="group-buy-add-wraper">
          <div class="act-img-wrap">
            <img src="/shop/static/imgs/dis.png" alt="" class="act-img-w-h">
          </div>
          <div class="act-tip-txt">
            <p>包装折扣</p>
            <p>引流&促销</p>
          </div>
          <div>
            <p>
              <el-button type="primary" class="sup-mini" @click="addDisShopPage" size="mini">立即创建</el-button>
            </p>
          </div>
        </div>-->

        <div class="group-buy-add-wraper">
          <div class="act-img-wrap">
            <img src="/shop/static/imgs/newuser.png" alt="" class="act-img-w-h">
          </div>
          <div class="act-tip-txt">
            <p>新人专享</p>
            <p>引流&促销</p>
          </div>
          <div>
            <el-button type="primary" class="sup-mini" @click="addNUPage" size="mini">立即创建</el-button>
          </div>
        </div>
      </div>

      <hr class="s-slip" />
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <region class="region-wrap" @RegionChange="RegionChange" :refresh="false"></region>
          <el-form-item label="活动名称">
            <el-input
              class="search-input"
              v-model.trim="searchContent.activityName"
              placeholder="请输入活动名称"
              size="small"
              @keyup.enter.native="doSearchHandel(true)"
              clearable
            ></el-input>
          </el-form-item>
          <el-form-item label="开始时间">
            <el-date-picker
              class="search-input"
              v-model="searchContent.activityStartDate"
              type="datetime"
              placeholder="请选择开始时间"
              format="yyyy-MM-dd HH:mm:ss"
              value-format="yyyy-MM-dd HH:mm:ss"
              size="small"
              @change="doSearchHandel(true)"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="结束时间">
            <el-date-picker
              class="search-input"
              v-model="searchContent.activityEndDate"
              type="datetime"
              placeholder="请选择结束时间"
              format="yyyy-MM-dd HH:mm:ss"
              value-format="yyyy-MM-dd HH:mm:ss"
              size="small"
              @change="doSearchHandel(true)"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="活动类型">
            <el-select
              class="search-input"
              v-model="searchContent.activityType"
              filterable
              placeholder="请选择类型"
              clearable
              size="small"
              @change="doSearchHandel(true)"
            >
              <el-option
                v-for="item in activityTypeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 搜索结果 活动状态 0：未发布，1 ：已发布 ，2：未开始，3：进行中，4：已结束，5：已终止，6：已失效"-->
    <div class="table-wraper tab-wraper" v-loading="loading">
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <el-tab-pane
          :name="item.activeName"
          v-for="(item, idx) in activityStatusOptions"
          :key="idx"
        >
          <span slot="label">{{item.label}} {{item.activeCount}}</span>
        </el-tab-pane>
        <my-table
          ref="myTable"
          @editItem="editItem"
          @showItem="showItem"
          @copyItem="copyItem"
          @publichItem="publichItem"
          @stopItem="stopItem"
          @deleteItem="deleteItem"
        ></my-table>
      </el-tabs>

      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
  import Region from '@/components/Common/StoreCommon/Region'
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import config from "@/utils/config"
  import MyTable from "./MyTable"
  const reqUrl = {
    // 删除活动
    delActivity: '/plateform/a/preSale/activity/delActivity',
    // 活动规则列表
    list: '/plateform/a/activityRule/list',
    // 更新活动规则状态
    updateStatus: '/plateform/a/activityRule/updateStatus',
    // 失效活动
    failActivity: '/plateform/a/preSale/activity/failActivity',
    // 发布活动
    publishActivity: '/plateform/a/preSale/activity/publishActivity'
  }
  export default {
    data() {
      return {
        pageSizes: config.pageSizes10,
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          DIFFCITY: true,
          regionCode: Storage.get('regionCode') || '', // 区域ID
          activityName: '', // 活动名称
          activityStartDate: '', // 开始时间
          activityEndDate: '', // 结束时间
          activityType: '', // 11 拼团 21 秒杀
          // 活动状态 0：未发布，1 ：已发布 ，2：未开始，3：进行中，4：已结束，5：已终止，6：已失效"
          activityStatus: ''
        },
        activityTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '拼团',
            value: 11
          },
          {
            label: '秒杀',
            value: 21
          },
          {
            label: '包装折扣',
            value: 31
          },
          {
            label: '新人专享',
            value: 41
          },
          {
            label: '每日爆款',
            value: 61
          }
        ],
        // 活动状态 0：未发布，1 ：已发布 ，2：未开始，3：进行中，4：已结束，5：已终止，6：已失效"
        activityStatusOptions: [
          {
            label: '全部',
            activeName: 'all',
            activeCount: 0,
            value: ''
          },
          {
            label: '进行中',
            activeName: 'activeName3',
            activeCount: 0,
            value: 3
          },
          {
            label: '未发布',
            activeName: 'activeName0',
            activeCount: 0,
            value: 0
          },
          {
            label: '未开始',
            activeName: 'activeName2',
            activeCount: 0,
            value: 2
          },
          {
            label: '已结束',
            activeName: 'activeName4',
            activeCount: 0,
            value: 4
          },
          {
            label: '已终止',
            activeName: 'activeName5',
            activeCount: 0,
            value: 5
          }
        ],
        // 门店列表下拉框
        storeOptions: [],
        tableData: [],
        // 点击切换的tab的名称
        activeName: 'all'
      }
    },
    mounted() {
      let searchContentMsg = Storage.get("searchContent")
      if(this.$route.meta.keepAlive && searchContentMsg) {
        this.pageNum = searchContentMsg.pageNum
        this.searchContent = searchContentMsg.searchContent
        this.activeName = searchContentMsg.tabName
      }
      Storage.remove('searchContent')
      this.handleTabClick({name: this.activeName})
    },
    components: {
      "my-table": MyTable,
      "region": Region
    },
    methods: {
      RegionChange (params) {
        this.searchContent.regionCode = params.regionCode
        this.doSearchHandel(true)
      },
      /**
       * [addGbPage 添加拼团活动]
       * 活动类型 :11-拼团，21-秒杀
       */
      addGbPage () {
        this.$router.push("/store_act/addgb/11")
      },
      /**
       * [addGbPage 添加秒杀活动]
       * 活动类型 :11-拼团，21-秒杀, 爆款-61
       */
      addTbPage () {
        this.$router.push("/store_act/addtb/21")
      },
      addNUPage () {
        this.$router.push("/store_act/addnu/41")
      },
      addBKPage () {
        this.$router.push("/store_act/addbk/61")
      },
      /**
       * [handleTabClick tab点击切换]
       * @return {[type]} [活动状态 0：未发布，1 ：已发布 ，2：未开始，3：进行中，4：已结束，5：已终止，6：已失效"]
       */
      handleTabClick (_val) {
        if (_val.name == 'all') {
          this.searchContent.activityStatus = ''
        } else if (_val.name == 'activeName0') {
          this.searchContent.activityStatus = 0
        } else if (_val.name == 'activeName1') {
          this.searchContent.activityStatus = 1
        } else if (_val.name == 'activeName2') {
          this.searchContent.activityStatus = 2
        } else if (_val.name == 'activeName3') {
          this.searchContent.activityStatus = 3
        } else if (_val.name == 'activeName4') {
          this.searchContent.activityStatus = 4
        } else if (_val.name == 'activeName5') {
          this.searchContent.activityStatus = 5
        } else if (_val.name == 'activeName6') {
          this.searchContent.activityStatus = 6
        }
        this.activeName = _val.name
        this.doSearchHandel(true)
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel 搜索]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel (_param) {
        let activityStartDateNum = this.searchContent.activityStartDate ? Date.parse(new Date(this.searchContent.activityStartDate.replace(/-/g, '/'))) : 0
        let activityEndDateNum = this.searchContent.activityEndDate ? Date.parse(new Date(this.searchContent.activityEndDate.replace(/-/g, '/'))) : 0
        if (this.searchContent.activityStartDate && this.searchContent.activityEndDate && !isNaN(activityStartDateNum) && !isNaN(activityEndDateNum)) {
          if (activityStartDateNum > activityEndDateNum) {
            this.$message("结束时间不能早于开始时间")
            return
          }
        }

        this.loading = true
        if (_param === true) {
          this.pageNum = 1
        }
        this.$http.post(reqUrl.list, { ...this.searchContent, pageSize: this.pageSize, pageNum: this.pageNum }).then(res => {
          this.loading = false
          if (res.code === 0) {
            if (res.data && res.data.pageVo && res.data.pageVo.result && res.data.pageVo.result.length) {
              this.tableData = res.data.pageVo.result
              let _value = {
                tableData: this.tableData
              }
              if (this.$refs.myTable) {
                this.$refs.myTable.setDefaultMesg(_value)
              }
              this.total = res.data.pageVo.total
            } else {
              this.tableData = []
              let _value = {
                tableData: this.tableData
              }
              if (this.$refs.myTable) {
                this.$refs.myTable.setDefaultMesg(_value)
              }
              for (let item of this.activityStatusOptions) {
                item.activeCount = 0
              }
              this.total = 0
            }
            for (let item of this.activityStatusOptions) {
              if (item.value === '') {
                item.activeCount = res.data.allCount || 0
              } else if (item.value === 3) {
                item.activeCount = res.data.goingCount || 0
              } else if (item.value === 0) {
                item.activeCount = res.data.unPublishedCount || 0
              } else if (item.value === 2) {
                item.activeCount = res.data.startedCount || 0
              } else if (item.value === 4) {
                item.activeCount = res.data.endCount || 0
              } else if (item.value === 5) {
                item.activeCount = res.data.stopCount || 0
              }
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [editItem 编辑]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      editItem(_row) {
        // 设置搜索条件
        let _paramMesg = {
          pageNum: this.pageNum,
          tabName: this.activeName,
          searchContent: this.searchContent
        }
        Storage.set("searchContent", _paramMesg)
        let activityId = _row.id
        let activityType = _row.activityType
        let activityStatus = _row.activityStatus
        if(activityType === 11) {
          this.$router.push(`/store_act/editgb/${activityId}/edit?status=${activityStatus}`)
        }else if(activityType === 21) {
          this.$router.push(`/store_act/edittb/${activityId}/edit?status=${activityStatus}`)
        }else if(activityType === 31) {
          this.$router.push(`/store_act/editdis/${activityId}/edit?status=${activityStatus}`)
        }else if(activityType === 41) {
          this.$router.push(`/store_act/editnu/${activityId}/edit?status=${activityStatus}`)
        }else if(activityType === 61) {
          this.$router.push(`/store_act/editbk/${activityId}/edit?status=${activityStatus}`)
        }else {
          this.$notify.warning({
            title: '温馨提示',
            message: '不存在改活动类型！'
          })
        }
      },
      /**
       * [showItem 查看]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      showItem(_row) {
        // 设置搜索条件
        let _paramMesg = {
          pageNum: this.pageNum,
          activeName: this.activeName,
          searchContent: this.searchContent
        }
        Storage.set("searchContent", _paramMesg)
        let activityId = _row.id
        let activityType = _row.activityType
        if(activityType === 11) {
          this.$router.push(`/store_act/showgb/${activityId}`)
        }else if(activityType === 21) {
          this.$router.push(`/store_act/showtb/${activityId}`)
        }else if(activityType === 31) {
          this.$router.push(`/store_act/showdis/${activityId}`)
        }else if(activityType === 41) {
          this.$router.push(`/store_act/shownu/${activityId}`)
        }else if(activityType === 61) {
          this.$router.push(`/store_act/showbk/${activityId}`)
        }else {
          this.$notify.warning({
            title: '温馨提示',
            message: '不存在改活动类型！'
          })
        }
      },
      /**
       * [copyItem 复制]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      copyItem(_row) {
        // 设置搜索条件
        let _paramMesg = {
          pageNum: this.pageNum,
          activeName: this.activeName,
          searchContent: this.searchContent
        }
        Storage.set("searchContent", _paramMesg)
        let activityId = _row.id
        let activityType = _row.activityType
        if(activityType === 11) {
          this.$router.push(`/store_act/editgb/${activityId}/copy`)
        }else if(activityType === 21) {
          this.$router.push(`/store_act/edittb/${activityId}/copy`)
        }else if(activityType === 31) {
          this.$router.push(`/store_act/editdis/${activityId}/copy`)
        }else if(activityType === 41) {
          this.$router.push(`/store_act/editnu/${activityId}/copy`)
        }else if(activityType === 61) {
          this.$router.push(`/store_act/editbk/${activityId}/copy`)
        }else {
          this.$notify.warning({
            title: '温馨提示',
            message: '不存在改活动类型！'
          })
        }
      },
      /**
       * [publichItem 发布]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      publichItem(_row) {
        let activityEndDate = _row.activityEndDate || ''
        let activityEndDateNum = Date.parse(new Date(activityEndDate.replace(/-/g, '/')))
        let currentDateNum = new Date().getTime()
        if(activityEndDate && !isNaN(activityEndDateNum)) {
          if(currentDateNum > activityEndDateNum) {
            this.$confirm('该活动已过期，是否继续发布？', '提示', {
              confirmButtonText: '继续',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              this.publichItemHandel(_row)
            }).catch(() => {})
          }else {
            this.$confirm('您确定要发布所选活动吗?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              this.publichItemHandel(_row)
            }).catch(() => {})
          }
        }
      },

      /**
       * [publichItemHandel 发布事件]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      publichItemHandel (_row) {
        let activityId = _row.id
        let _params = {
          DIFFCITY: true,
          activityId: activityId,
          activityStatus: 1
        }
        let requestUrl = _row.activityType === 41 ? reqUrl.publishActivity : reqUrl.updateStatus
        this.$http.post(requestUrl, { ..._params }).then(res => {
          if (res.code === 0) {
            this.$message.success("发布成功")
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [stopItem 终止]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      stopItem (_row) {
        let activityId = _row.id
        let _params = {
          DIFFCITY: true,
          activityId: activityId,
          activityStatus: 5
        }
        let requestUrl = _row.activityType === 41 ? reqUrl.failActivity : reqUrl.updateStatus
        this.$confirm('您确定要终止所选活动吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(requestUrl, { ..._params }).then(res => {
            if (res.code === 0) {
              this.$message.success("终止成功")
              this.doSearchHandel()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => { })
      },
      /**
       * [deleteItem 删除活动]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteItem(_row) {
        let activityId = _row.id
        this.$confirm('您确认删除当前未发布的活动吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(reqUrl.delActivity, {activityId: activityId, DIFFCITY: true}).then(res => {
            if(res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      }
    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next) {
      from.meta.keepAlive = false;
      next();
    }
  }
</script>



