<style>
.p-m-w {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.img-w {
  width: 80px;
  height: 80px;
}
.img-w > img {
  width: 80px;
}
.p-m-c-r {
  padding: 0 10px;
}
.erp-p-no {
  color: #666;
  font-size: 12px;
}

.stock-num .el-button--mini {
  padding: 6px 5px;
}
.sync-file {
  color: red;
  cursor: pointer;
}
</style>
<template>
  <div>
    <el-table
      class="fixed-height"
      :data="tableData"
      empty-text="没有查询到您想要的数据"
      style="width:100%;"
      size="small"
    >
      <el-table-column min-width="70px" prop="id" label="活动ID"></el-table-column>
      <el-table-column
        min-width="90px"
        prop="activityType"
        label="活动类型"
        :formatter="activityTypeFormater"
      ></el-table-column>
      <el-table-column min-width="160px" prop="activityCode" label="活动编码"></el-table-column>
      <el-table-column min-width="150px" prop="activityName" label="活动名称"></el-table-column>
      <el-table-column min-width="170px" label="活动时间">
        <template slot-scope="scope" size="small">
          <div>
            <p>{{scope.row.activityStartDate}}</p>
            <p>至</p>
            <p>{{scope.row.activityEndDate}}</p>
          </div>
        </template>
      </el-table-column>
      <el-table-column min-width="86px" prop="regionName" label="活动区域"></el-table-column>
      <el-table-column min-width="90px" prop="storeNum" label="参与门店数"></el-table-column>
      <el-table-column min-width="90px" prop="goodsNum" label="参与商品数"></el-table-column>
      <el-table-column
        min-width="120px"
        prop="activityStatus"
        label="活动状态"
        :formatter="activityStatusFormater"
      ></el-table-column>
      <el-table-column fixed="right" label="操作" width="140">
        <template slot-scope="scope" size="small">
          <div>
            <div>
              <el-button v-if="scope.row.activityStatus === 0" @click="deleteItem(scope.row)" type="text" size="mini" icon="el-icon-delete">删除</el-button>
              <el-button v-else type="text" size="mini" icon="el-icon-view" @click="showItem(scope.row)">查看</el-button>
              <el-button
                type="text"
                size="mini"
                icon="el-icon-tickets"
                @click="copyItem(scope.row)"
              >复制</el-button>
            </div>
            <div>
              <!-- 未发布，未开始[发布中]，进行中[发布中] 都可以编辑 -->
              <!-- 未开始[发布中]，进行中[发布中] 编辑的时候只能添加商品 -->
              <el-button v-if="scope.row.activityStatus === 0 || (scope.row.activityStatus === 2 && scope.row.activityType !== 11) || (scope.row.activityStatus === 3 && scope.row.activityType !== 11)" type="text" size="mini" icon="el-icon-edit" @click="editItem(scope.row)">编辑</el-button>
              <el-button v-if="scope.row.activityStatus === 0" type="text" size="mini" icon="el-icon-upload2" @click="publichItem(scope.row)">发布</el-button>
              <el-button v-if="(scope.row.activityStatus === 2 || scope.row.activityStatus === 3 || scope.row.activityStatus === 4) && scope.row.activityType === 11" type="text" size="mini" icon="el-icon-warning" @click="stopItem(scope.row)">终止</el-button>
              <el-button v-if="(scope.row.activityStatus === 2 || scope.row.activityStatus === 3) && (scope.row.activityType === 21 || scope.row.activityType === 31 || scope.row.activityType === 41 || scope.row.activityType === 61)" type="text" size="mini" icon="el-icon-warning" @click="stopItem(scope.row)">终止</el-button>
            </div>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data () {
    return {
      tableData: []
    }
  },
  methods: {
    /**
     * [setDefaultMesg 设置信息]
     * @param {[type]} _val [description]
     */
    setDefaultMesg (_val) {
      this.tableData = _val.tableData
    },
    /**
     * [activityTypeFormater 活动格式化]
     * @param  {[type]} row       [description]
     * @param  {[type]} column    [description]
     * @param  {[type]} cellValue [description]
     * @return {[type]}           [description]
     */
    activityTypeFormater (row, column, cellValue) {
      if (cellValue === 11) {
        return "拼团"
      } else if (cellValue === 21) {
        return "秒杀"
      } else if (cellValue === 31) {
        return "包装折扣"
      } else if (cellValue === 41) {
        return "新人专享"
      } else if (cellValue === 61) {
        return "每日爆款"
      } else {
        return cellValue
      }
    },
    // 搜索结果 活动状态 0：未发布，1 ：已发布 ，2：未开始，3：进行中，4：已结束，5：已终止，6：已失效"
    activityStatusFormater (row, column, cellValue) {
      if (cellValue === 0) {
        return "未发布"
      } else if (cellValue == 1) {
        return "已发布"
      } else if (cellValue == 2) {
        return "未开始[发布中]"
      } else if (cellValue == 3) {
        return "进行中[发布中]"
      } else if (cellValue == 4) {
        if (row.activityType === 11) {
          return "已结束[发布中]"
        } else {
          return "已结束"
        }
      } else if (cellValue == 5) {
        return "已终止"
      } else if (cellValue == 6) {
        return "已失效"
      }
    },
    stopItem (_row) {
      this.$emit("stopItem", _row)
    },
    editItem (_row) {
      this.$emit("editItem", _row)
    },
    showItem (_row) {
      this.$emit("showItem", _row)
    },
    copyItem (_row) {
      this.$emit("copyItem", _row)
    },
    publichItem (_row) {
      this.$emit("publichItem", _row)
    },
    deleteItem (_row) {
      this.$emit("deleteItem", _row)
    }

  }
}
</script>



